import React, { useState } from 'react';
import Encryption from '../crypto/encryption.js';
import math from '../utils/math.js';
import Config from '../utils/config.js';
import types from '../utils/types.js';
import './WalletCard.css'

class EncDataTestClass extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            encSystem: null,
            data: [],
            privKey: null,
            encryptedData: [],
            recoveredData: [],
        }
        this.generateRandomFileData = this.generateRandomFileData.bind(this);
        this.onChangePrivKey = this.onChangePrivKey.bind(this);
        this.generateCipherTextHandler = this.generateCipherTextHandler.bind(this);
        this.recoverDataHandler = this.recoverDataHandler(this);
    }

    generateRandomFileData(e){
        const tmp = [];
        for(let i=0; i<Config.dataMaxBlockNum; i++){
            tmp.push(math.randomFieldElement().toString(16));
        }
        this.setState({data : tmp});
    }

    onChangePrivKey(e) {
        console.log("priv : ", e.target.value)
        this.setState({ privKey : e.target.data });
    }

    generateCipherTextHandler(e) {
        // if (this.state.data.length != Config.dataMaxBlockNum){
        //     alert("invalid data length");
        //     return;
        // }

        // if (this.state.privKey == null){
        //     alert("invalid privKey");
        //     return;
        // }

        const symEnc = new Encryption.symmetricKeyEncryption(this.state.privKey);
        this.setState({encSystem : symEnc});
        const sct = symEnc.EncData(this.state.data);
        this.setState({encryptedData : sct.toJson()});
        console.log("encData : ", sct.toJson());
    }

    recoverDataHandler(e) {
        // if(this.state.encryptedData.length != Config.dataMaxBlockNum){
        //     alert("does not have Ct");
        //     return;
        // }
        const tmp = this.state.encSystem.DecData(Encryption.sCTdata.fromJson(this.state.encryptedData));
        console.log(tmp);
        this.setState({recoveredData : tmp});
    }



    render(){
        return(
            <div className='mimcCard'>
                <h3> -- encryption test --</h3>
                <div>
                <button onClick={this.generateRandomFileData}> get random BigInt fileData </button>
                <h4>file Data : {this.state.data.toString()}</h4>
            </div>
            <div >
                input your privKey :
                <input onChange={this.onChangePrivKey} placeholder="input your privKey"/>
            </div>
            <div className='mimcCard'>
                <button onClick={ this.generateCipherTextHandler}> get Peer Data </button>
                <h4>enc Data : {this.state.encryptedData.toString()} </h4>
            </div>
            
            <div>
                <button onClick={this.state.recoverDataHandler}> Decrypt Data</button>
                <h4>dec data : {this.state.recoveredData.toString()}</h4>
            </div>
            <h4> state : {this.state.toJson()}</h4>
            </div>
        );
    }
}

function EncData() {
    // let dataBuf;
    const [encSystem, setEncSystem] = useState(null);
    const [fileData, setFileData] = useState([]);
    const [privKey, setPrivKey]   = useState(null);
    const [encData, setEncData]   = useState([]);
    const [decData, setDecData]   = useState([]);

    const onClcikRandomFileData = () => {
        const tmp = [];
        for(let i=0; i<Config.dataMaxBlockNum; i++){
            tmp.push(math.randomFieldElement().toString(16));
        }
        
        setFileData(tmp);
    };

    const onChangeprivKey = (e) => {
        console.log(e.target.value);
        this.setState({privKey: e.target.value})
    };

    const getPeerData = async () => {
        console.log("priv Key  : ", privKey);
        console.log("file Data : ", fileData);
        const peerData = new Encryption.symmetricKeyEncryption(privKey);
        setEncSystem(peerData);
        const sct = peerData.EncData(fileData);
        console.log("sct : ", sct.toJson());
        setEncData(sct.toJson());
        console.log("encData : ", sct.toJson());
    };

    const getDecData = () => {
        console.log(encData);
        const tmp = encSystem.DecData(Encryption.sCTdata.fromJson(encData));
        console.log(tmp);
        setDecData(tmp);
    }

    const getFileData = () => {
        return fileData;
    };

    const getEncData = () => {
        return encData;
    };


    return(
        <div className='mimcCard'>
            <h3> -- encryption test --</h3>
            <div>
                <button onClick={onClcikRandomFileData}> get random BigInt fileData </button>
                <h4>file Data : {fileData.toString()}</h4>
            </div>
            <div >
                input your privKey :
                <input onChange={onChangeprivKey} placeholder="input your privKey"/>
            </div>
            <div className='mimcCard'>
                <button onClick={ getPeerData}> get Peer Data </button>
                <h4>enc Data : {encData.toString()} </h4>
            </div>
            
            <div>
                <button onClick={getDecData}> Decrypt Data</button>
                <h4>dec data : {decData}</h4>
            </div>
            <h4></h4>
        </div>
    );
}

const EncDataTest = {
    EncData,
    EncDataTestClass,
};

export default EncDataTest;




